/*HÄMTA DATA FRÅN API */
/*POLISEN: https://polisen.se/api/events */
window.onload = init;



function init(){
processData();
}



// En asynkron funktion som simulerar hämtning av data från en API

async function fetchData() {
try {

const response = await fetch('https://polisen.se/api/events');

const data = await response.json();

return data;

} catch (error) {

console.error('Fetch error:', error);

throw error;

}
}



// Användning av den asynkrona funktionen
async function processData() {

try {

const result = await fetchData(); // Väntar på att data ska hämtas

console.log(result);

let event = Object.keys(data.type);
showEvents(event);




} catch (error) {

    return console.log('Försöket lyckades inte');
// Hantera fel om det uppstår vid hämtning eller bearbetning av data

}

}

function showEvents(event){
    const eventTypeEl = document.getElementById("test-polis");

    event.forEach(ev => {
        let newEl = document.createElement("p");
        let newText = document.createTextNode(ev);
        newEl.appendChild(newText);

        eventTypeEl.appendChild(newEl);
    });
}




